* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  body {
    background-color: rgb(229, 106, 245);
    font-family: "Poppins", sans-serif;
    color: rgb(0, 0, 0);
    min-height: 100vh;
  }
  header,
  form {
    display: flex;
    min-height: 25vh;
    justify-content: center;
    align-items: center;
  }
  form input,
  form button {
    padding: 1.5rem;
    border: none;
    font-size: 1.6rem;
    background: white;
  }
  form {
    button {
      color: #1900ff;
      background: rgba(255, 255, 255, 0.753);
      cursor: pointer;
      transition: all 0.5s ease;
      &:hover {
        background: #00ff22;
        color: white;
      }
    }
  }
  .todo_container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .todo_list {
    min-width: 35%;
    list-style: none;
    list-style: circle;
    list-style: inside;
  }
  .todo {
    margin: 5px auto;
    background: rgba(255, 255, 255, 0.788);
    color: #000000;
    display: flex;
    font-size: 1.2rem;
    padding: 0.3rem;
    justify-content: space-between;
    align-items: center;
    transition: all 0.5s ease;
    li {
      flex: 1;
    }
  }
  .complete_btn,
  .delete_btn {
    padding: 0.5rem;
    background: #ff3700;
    color: white;
    border: none;
    margin-left: 0.2rem;
    cursor: pointer;
    font-size: 1rem;
  }
  .complete_btn {
    background: rgb(0, 255, 0);
    &:active {
      background: rgb(0, 255, 0);
    }
  }
  .delete_btn {
    &:active {
      background: #ff0000;
    }
  }
  .fa-trash,
  .fa-check {
    pointer-events: none;
  }
  .completedItem {
    text-decoration: line-through;
  }
  .delete {
    transform: rotateZ(0deg);
  }
  